.CatBlock
{
    width:190px;
    height: 90px;
    background: #240b41;
    border-radius: 22px;
    margin-left:15px;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, .25);
}

.CatItemImg
{
    float: left;

    width: 50px;
    height: 50px;
    border-radius: 50px;

    margin-top: 20px;
    margin-left: 10px;
    background: #19082c;
}

.CatText
{
    float: right;
}

.CatItemText
{
    margin-left: 15px;
    margin-top: 25px;
    font-weight: 600;
    font-size: 14px;
}

.CatItemTextDesc
{
    color: #a1a1a1;
    font-size: 13px;
    margin-left: 15px;
}
