.Header
{
    display: block;
    height: auto;
    padding-bottom: 20px;

    color: #ffffff;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

    background: #19082c;
}

.HeaderImg
{
    width: 40px;
    height: 40px;

    border-radius: 50px;
}

.InfoHeader
{
    margin-top: -20px;
    margin-bottom: 60px;    
}

.InfoUser
{
    float:left;
    margin-left: -20px;
}

.InfoImg
{
    float: right;
}

.HeaderImg
{
    float:left;
}

.InfoNotice
{
    float: right;

    padding-top: 12px;
}

.TextBold
{
    font-weight: 600;
    font-size: 16px;
}

.TextSmall
{
    font-size: 13px;
}
.CatBlock
{
    width:190px;
    height: 90px;
    background: #240b41;
    border-radius: 22px;
    margin-left:15px;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, .25);
}

.CatItemImg
{
    float: left;

    width: 50px;
    height: 50px;
    border-radius: 50px;

    margin-top: 20px;
    margin-left: 10px;
    background: #19082c;
}

.CatText
{
    float: right;
}

.CatItemText
{
    margin-left: 15px;
    margin-top: 25px;
    font-weight: 600;
    font-size: 14px;
}

.CatItemTextDesc
{
    color: #a1a1a1;
    font-size: 13px;
    margin-left: 15px;
}

.BlockName
{
    margin: 5px;
}

.TitleBlock
{
    font-weight: 600;
    font-size:20px;
}

.AboutBlock
{
    margin-top: 1px;
    font-size:14px;
    color: #B9B9B9;
}
:root {
    --background_page: #19082c;
    --header_background: #19082c;
}

.Container
{
    display: block;
    height: auto;

   

    background: #ffffff;

    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}

.HorScroll
{
    margin-left: -15px;
}
